import React, { Component } from "react";

class FormTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="row">
        <div className="col-md-3 form-group">
          <label htmlFor="">Kode Barang</label>
          <select name="kode_barang" id="kode_barang" className="form-control">
            <option value="BARANG 1">BARANG 1</option>
            <option value="BARANG 2">BARANG 2</option>
          </select>
        </div>
        <div className="col-md-4 form-group">
          <label htmlFor="">Keterangan</label>
          <input name="keterangan" id="keterangan" className="form-control" />
        </div>
        <div className="col-md-5"></div>
        <div className="col-md-2 form-group">
          <label htmlFor="">Kadar (%)</label>
          <input name="Kadar" id="Kadar" className="form-control" />
        </div>
        <div className="col-md-2 form-group">
          <label htmlFor="">pkg (Gr)</label>
          <input name="pkg" id="pkg" className="form-control" />
        </div>
        <div className="col-md-2 form-group">
          <label htmlFor="">pkg (Gr)</label>
          <input name="pkg" id="pkg" className="form-control" />
        </div>
        <div className="col-md-2 form-group">
          <label htmlFor="">Bruto (Gr)</label>
          <input name="bruto" id="bruto" className="form-control" />
        </div>
        <div className="col-md-2 form-group">
          <label htmlFor="">Harga (%)</label>
          <input name="Harga" id="Harga" className="form-control" />
        </div>
        <div className="col-md-2 form-group">
          <label htmlFor="">Netto (Gr)</label>
          <input name="Netto" id="Netto" className="form-control" />
        </div>
        <div className="col-md-4 form-group">
          <label htmlFor="">Ongkos (Rp)</label>
          <input name="Ongkos" id="Ongkos" className="form-control" />
        </div>
        <div className="col-md-12 form-group">
          <div className="text-right">
            <button className="btn btn-primary">SIMPAN</button>
          </div>
        </div>
      </div>
    );
  }
}

export default FormTambahBarang;
