import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import FormTambahBarang from "./Component/FormTambahBarang";
import axios from "axios";

const columns = [
  {
    dataField: "No",
    text: "No",
  },
  {
    dataField: "KodeBarang",
    text: "Kode Barang",
  },
  {
    dataField: "keterangan",
    text: "Keterangan",
  },
  {
    dataField: "kat_harga",
    text: "Kat Harga",
  },
  {
    dataField: "Kadar",
    text: "Kadar",
  },
  {
    dataField: "pkg_gr",
    text: "Pkg (Gr)",
  },
  {
    dataField: "Bruto",
    text: "Bruto (Gr)",
  },
  {
    dataField: "harga",
    text: "Harga",
  },
  {
    dataField: "netto",
    text: "Netto (Gr)",
  },
  {
    dataField: "ongkos",
    text: "Ongkos (Rp)",
  },
  {
    dataField: "Qty",
    text: "Qty",
  },
];

const products = [
  {
    kode_salesman: "001",
    nama_salesman: "BANDUNG",
  },
  {
    kode_salesman: "002",
    nama_salesman: "BOGOR",
  },
];

const server = process.env.REACT_APP_BACKEND_URL;

class PindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dari: [],
      kepada: [],
      kategori: [],
    };
  }

  componentDidMount() {
    axios
      .get(server + "suppliers")
      .then((res) =>
        this.setState({
          dari: res.data,
          kepada: res.data,
        })
      )
      .catch((err) => alert(err));
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Pindah Barang
                </h5>
                <div className="row">
                  <div className="col-md-3 form-group">
                    <label>Tanggal</label>
                    <input
                      type="date"
                      id="tanggal"
                      name="tannggal"
                      className="form-control"
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Dari</label>
                    <select id="dari" name="dari" className="form-control">
                      {this.state.dari.map((dari) => (
                        <option
                          value={dari.kodeSupplier}
                          key={dari.kodeSupplier}
                        >
                          {dari.namaSupplier}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-1 text-center">
                    <label htmlFor="" className="text-white">
                      .
                    </label>
                    <h3>
                      <i className="fa fa-exchange-alt"></i>
                    </h3>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Ke</label>
                    <select id="dari" name="dari" className="form-control">
                      {this.state.dari.map((dari) => (
                        <option
                          value={dari.kodeSupplier}
                          key={dari.kodeSupplier}
                        >
                          {dari.namaSupplier}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Kategori</label>
                    <select
                      id="kategori"
                      name="kategori"
                      className="form-control"
                    >
                      <option value="" key="12">
                        AYU GOLD
                      </option>
                      <option value="" key="22">
                        AYU GOLD
                      </option>
                    </select>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Bon Pindah</label>
                    <input
                      type="text"
                      id="bon_pindah"
                      name="bon_pindah"
                      className="form-control"
                    />
                  </div>
                  <div className="col-lg-4"></div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-left">
                      <button className="btn btn-primary" data-toggle="modal">
                        SIMPAN
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                      >
                        BATAL
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-center">
                      <button className="btn btn-primary" data-toggle="modal">
                        BARCODE (+)
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                      >
                        BARCODE (-)
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        data-toggle="modal"
                        data-target="#primary-header-modal"
                      >
                        TAMBAH BARANG
                      </button>
                    </div>
                  </div>
                  <div className="col-md-12 col-lg-12">
                    <BootstrapTable
                      keyField="KodeBarang"
                      data={products}
                      columns={columns}
                      bordered={true}
                      headerClasses="header-table"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Salesman
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormTambahBarang />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PindahBarang;
